  //       next();
  //       NProgress.done();
  //     }
  //   } else {
  //     if (whiteList.indexOf(to.path) !== -1) {
  //       // 在免登录白名单中，直接进入
  //       next();
  //     } else {
  //       Message.error("请先登录");
  //       // 其他没有访问权限的页面将被重定向到登录页面
  //       next(`/login?redirect=${to.path}`);
  //       NProgress.done();
